import React, { useState, useEffect } from "react";
import "./Land.css";

import landAgg from "../../public/obj_g_c/createLandAgg.js";
import win from "../../public/obj_g_c/window";


function Land() {
    return <>
        <div className="land">
            <Money></Money>
            {
                landAgg.landAgg.map( (k, i) => {
                    return <LandDan k={k} i={i} key={i}></LandDan>
                } )
            }
        </div>
    </>
}

// 钱组件
function Money() {
    const mon = win.getMoney();
    const [money, setMoney] = useState(mon);
    win.setMoneyXuan(setMoney);
    return <div className="money">金币: {money}</div>
}


function LandDan(props) {
    const index = props.i;
    const land = props.k;
    // 得到不同阶段农作物的图片
    const cropsBackImgSrc = land.getCropsBackImgSrc();
    // 得到不同阶段土地的图片
    const landBackImgSrc = land.getLandBackImgSrc();
    // 得到收获标识
    const harvest = land.harvestJudge();
    
    // 由于农作物展示图片，与土地图片可能发生变化，所以要创建出对应的状态
    const [cropsImg, setCropsImg] = useState(cropsBackImgSrc);
    const [landImg, setLandImg] = useState(landBackImgSrc);
    const [har, setHar] = useState(harvest);

    // 开启定时器，土地背景图与农作物阶段展示图是会随着时间的改变而改变
    useEffect( () => {
        setInterval( () => {
            // 得到不同阶段农作物的图片
            const cropsBackImgSrc = land.getCropsBackImgSrc();
            // 得到不同阶段土地的图片
            const landBackImgSrc = land.getLandBackImgSrc();
            // 得到收获标识
            const harvest = land.harvestJudge();
            // 重新设置状态
            setCropsImg(cropsBackImgSrc);
            setLandImg(landBackImgSrc);
            setHar(harvest);
        }, 1000 )
    }, [])

    return <div className={`t_${index + 1}`} style={ {
        background: `url(${landImg}) 0% 0% / 100% 100% no-repeat`
    } } onClick={ () => {
        if( !land.judgeLandContainCrops() ) {
            React.sum.emit("setExhibitionBoo", "种子包种植");
            // 注册该土地的种植方法
            React.sum.on("plantCrops", (crops) => {
                land.plantCrops(crops);
            } );
        }
    } }>
        <div className="watering" style={ {
            // 湿润度达到一定条件后，才显示浇水按钮
            display: land.wettability <= 2 ? "block" : "none"
        } } onClick={ (e) => {
            // 点击进行浇水
            land.watering();
            e.stopPropagation();
        } }>浇水</div>
        <div className="insecticidal" style={ {
            display: land.insecticidalIden ? "block" : "none"
        } }>除虫</div>
        <div className="harvest" style={ {
            display: har ? "block" : "none"
        } } onClick={ () => {
            land.harvest();
        } }>收获</div>
        <div className="remove" style={ {
            display: land.removeTips() ? "block" : "none"
        } } onClick={ (e) => {
            land.removeCrops();
            e.stopPropagation();
        } }>摘除</div>

        {cropsImg && <img className={ land.getStageClass() } src={cropsImg} alt="" />}
    </div>
}

export default Land;