import cropsAgg from "./createCropsAgg.js";
import Seed from "./createSeed.js";
import seedPack from "./createSeedPack.js";
import win from "./window";

class Shop {
    constructor() {
        this.seedAgg = [];

        this.createSeed();
    }
    // 创建商品列表
    createSeed() {
        const crops = cropsAgg.cropsAgg;
        for(let i = 0; i < crops.length; i ++) {
            this.seedAgg.push( new Seed(crops[i]) );
        }
    }
    // 购买商品函数
    buySeed(seed, num) {
        // 获取到当前共有多少金币
        const money = win.getMoney();
        // 计算出购买种子需要的金币
        const m = seed.money * num;
        if(money > m) {
            seedPack.seedNumPlus(seed, num);
            win.setMoney(money - m);
            alert("购买成功");
        }
        else {
            alert("金币不足");
        }
    }
    reedNumIncrease(num) {
        num += 1;
        if(num === 100) num = 99;
        return num;
    }
    reedNumReduce(num) {
        num -= 1;
        if(num === 0) num = 1;
        return num;
    }
}

// 创建商店对象
const shop = new Shop();

export default shop;