import url from "./url";

// 农作物对象的构造函数
class Crops {
    constructor(name, stageTime, fruitMoney, seedMoney, maturityTime, harvestNum, harvestFruitNum) {
        this.name = name;
        this.fruitMoney = fruitMoney;
        this.seedMoney = seedMoney;
        this.maturityTime = maturityTime;
        this.harvestNum = harvestNum;
        this.stageTime = stageTime;
        this.stageImgSrc = [];
        this.harvestFruitNum = harvestFruitNum;
        this.fruitImgSrc = url + `/project/nongChang/img/作物/${name}/${name}-种子.png`
        this.witheredImgSrc = url + `/project/nongChang/img/作物/${name}/${name}-结束.png`
    
        this.handleTimeStage();
        this.urlIndexes();
    }
    // 阶段获取
    getStage(plantTime) {
        // 获取到当前的时间
        const time = new Date().getTime();
        // 与各个阶段的时间进行比较
        for(let i = 0; i < this.stageTime.length; i ++) {
            if( time >= this.stageTime[i] * 1000  + plantTime) {
                if( this.stageTime[i + 1] && time < this.stageTime[i + 1] * 1000 + plantTime ) {
                    return i;
                }
                else if(! this.stageTime[i + 1]) return i;
            }
        }
    }


    // 处理阶段时间，转换格式
    handleTimeStage() {
        const stageTime = [];
        this.stageTime.forEach( (k, i) => {
            const splitData = k.split("-");
            const time = 86400 * splitData[0] + 3600 * splitData[1] + 60 * splitData[2] + (+splitData[3]);
            stageTime.push(time);
        } )
        this.stageTime = stageTime;
    }
    // 创建各个图片的地址索引
    urlIndexes() {
        this.stageTime.forEach( (k, i) => {
            if(i === 0) this.stageImgSrc.push(url + `/project/nongChang/img/作物/${this.name}/${this.name}-启动.png`);
            else this.stageImgSrc.push(url + `/project/nongChang/img/作物/${this.name}/${this.name}-${i}.png`);
        } )
    }
}

export default Crops;