import seed from "./createSeedPack.js";
import house from "./createHouse.js";
import ajax from "./ajax.js";
import crops from "./createCropsAgg.js";
import url from "./url";

class Land {
    constructor(i, x, y, width, height) {
        this.wettability = 4.86400;
        this.plantTime = 0;
        this.cropsAgg = null;
        this.fruitNum = 0;
        this.fruitTime = 0;
        this.weedIden = false;
        this.insecticidalIden = false;
        this.currentHarvestNum = 0;
        this.i = i;
        
        this.x = x;
        this.y = y;
        this.width = width;
        this.height = height;

        // 开启定时器
        this.setInterval();
        this.ajaxGetLand();
    }
    // 种植农作物的方法
    plantCrops(cropsAgg) {
        // 没有农作物，可以进行种植
        if( !this.judgeLandContainCrops() ) {
            // 保存种植时间
            this.plantTime = new Date().getTime();
            // 农作物对象赋值
            this.cropsAgg = cropsAgg;
            // 种子数量减少
            seed.remove(cropsAgg.name);

            // 发送请求，进行数据的保存
            this.ajaxSetLand();
            alert("种植成功");
        }
    }
    // 获取土地背景图片的方法
    getLandBackImgSrc() {
        // 判断湿润度是否小于1
        if( this.wettability < 1) this.wettability = 1;
        let wettability = 5 - parseInt(this.wettability);
        let imgSrc = url + `/project/nongChang/img/土地/土地-${wettability}.png`;
        if( this.weedIden ) imgSrc = url + `/project/nongChang/img/土地/土地-草.png`;
        return imgSrc;
    }
    // 获取当前土地中所种农作物对应阶段的图片路径
    getCropsBackImgSrc() {
        // 判断当前土地上是否有农作物
        if( !this.judgeLandContainCrops() ) return;

        // 获取到具体的阶段
        const stage = this.cropsAgg.getStage( this.plantTime );
        let imgSrc = this.cropsAgg.stageImgSrc[stage];
        if( this.witheredJudge() ) imgSrc = this.cropsAgg.witheredImgSrc;
        return imgSrc;
    }  
    // 枯萎判断
    witheredJudge() {
        if(this.currentHarvestNum === this.cropsAgg.harvestNum) {
            return true
        }
        return false;
    }
    // 是否可以收获判断
    harvestJudge() {
        // 没有农作物不进行收获判断，一定不可以收获 
        if( !this.judgeLandContainCrops() ) return false;
        // 查看当前处于第几阶段，如果是最后一个阶段，表示进入收获阶段
        const stage = this.cropsAgg.getStage(this.plantTime);
        
        if( stage === this.cropsAgg.stageTime.length - 1 ) {
            const stageTime = this.fruitTime + this.cropsAgg.maturityTime * 1000 * this.currentHarvestNum;
            const time = new Date().getTime();
            if(time >= stageTime && !this.witheredJudge()) return true;
        }
        return false;
    }
    weed() {

    }
    insecticidal() {

    }
    // 更新湿润度
    wettabilityGeng() {
        // 判断是否有农作物，没有不进行操作
        if( !this.cropsAgg ) return;
        // 获取到当前的时间
        const timeDang = new Date().getTime();
        // 获取到种植的时间
        const timeZhong = this.plantTime;
        // 算出时间差
        const time = parseInt((timeDang - timeZhong) / 1000);
        // 湿润度变化
        this.wettability -= time * 0.00001;
    }
    // 浇水函数
    watering() {
        this.wettability = 4.9;

        // 发送请求，进行数据的保存
        this.ajaxSetLand();
        alert("浇水成功");
    }
    // 收获方法
    harvest() {
        this.currentHarvestNum += 1;
        this.fruitTime = new Date().getTime();
        
        // 收获多少果实
        const num = parseInt(this.cropsAgg.harvestFruitNum * parseInt(this.wettability) / 4);
        house.addFruit(this.cropsAgg, num);

        // 发送请求进行数据的保存
        this.ajaxSetLand();
    }
    // 判断当前土地上是否有农作物
    judgeLandContainCrops() {
        if(this.cropsAgg) return true;
        return false;
    }
    // 去除农作物函数
    removeCrops() {
        this.plantTime = 0;
        this.cropsAgg = null;
        this.fruitNum = 0;
        this.fruitTime = 0;
        this.currentHarvestNum = 0;

        // 发送请求进行数据的保存
        this.ajaxSetLand();
    }
    removeTips() {
        // 判断当前土地上是否有农作物
        if( !this.judgeLandContainCrops() ) return false;
        if( this.witheredJudge() ) return true;
        return false;
    }
    wettabilityDec() {

    }
    // 获取不同阶段，对应的不同的class名称
    getStageClass() {
        // 判断当前土地上是否有农作物
        if( !this.judgeLandContainCrops() ) return;

        // 获取到具体的阶段
        const stage = this.cropsAgg.getStage( this.plantTime );
        if(stage === 0) return "qiDong";
        else if(stage === 1) return "zuoWu-1";
        else if(stage === 2) return "zuoWu-2";
        else if(stage === 3) return "zuoWu-3";

        if( this.witheredJudge() ) return "jieShu"
        
        return "";
    }
    // 开启定时器
    setInterval() {
        setInterval(() => {   
            // 湿润度递减
            this.wettability -= 0.00001 * 100;
            
            // 发送请求，进行数据的保存
            this.ajaxSetLand();
        }, 100000)
    }
    ajaxGetLand() {
        ajax(url + `/nongChang/getLand?name=liu&index=${this.i}`)
        .then( (data) => {
            const d = JSON.parse(data);
            if(typeof d.data === "string") d.data = JSON.parse(d.data);
            if(d.type === "ok" && typeof d.data === "object") {
                for(const prop in this) {
                    if( d.data[prop] && this.hasOwnProperty(prop) ) {
                        if(prop === "cropsAgg") {
                            this.cropsAgg = crops.getCrops(d.data[prop].name);
                        }else this[prop] = d.data[prop];
                    }
                }
            }
            
            // 更新湿润度
            this.wettabilityGeng();
        } )
    }
    ajaxSetLand() {
        const data = JSON.stringify( this );
        const d = JSON.stringify({
            name: 'liu',
            index: this.i,
            land: data
        })
        ajax(url + `/nongChang/setLand`, "POST", d)
    }
}


export default Land;