import React from "react";
import "./Menu.css";
import url from "../../public/obj_g_c/url";

function Menu() {
    return <>
        <div className="menu">
            <div onClick={ iconClick.bind(null, "商店") }>
                <img src={url + "/project/nongChang/img/图标/商店.png"} alt="" prop="" />
            </div>
            <div onClick={ iconClick.bind(null, "仓库") } >
                <img src={url + "/project/nongChang/img/图标/仓库.png"} alt="" prop="" />
            </div>
            <div onClick={ iconClick.bind(null, "种子包") } >
                <img src={url + "/project/nongChang/img/图标/种子包.png"} alt="" prop="" />
            </div>
        </div>
    </>
}

// 各个图标点击的事件函数
function iconClick(name) {
    // 改变Exhibition组件中的状态
    React.sum.emit("setExhibitionBoo", name);
}

export default Menu;