import React from "react";
import "./Seed.css";

import seedPack from "../../../public/obj_g_c/createSeedPack.js";


function Seed() {
    return <>
        <div className="seed">
            <ul>
                { 
                    seedPack.seedAgg.map((k, i) => {
                        return <SeedRend k={k} key={i}></SeedRend>
                    })
                }
            </ul>
        </div>
    </>
}

// 每一个种子的渲染组件
function SeedRend(props) {
    const seedAgg = props.k;
    return <li>
        <div className="img">
            <img src={seedAgg.seed.seedImg} alt="" prop="" />
        </div>
        <div className="num">数量: {seedAgg.num}</div>
    </li>
}

export default Seed;