import React from "react";
import ReactDOM from "react-dom";
import App from "./App.js";

React.sum = ( () => {
    const funAgg = {};
    return {
        on(name, fun) {
            funAgg[name] = fun;
        },
        emit(name, ...data) {
            if( funAgg[name] ) funAgg[name](...data);
            else {
                new Error(`${name}方法没有被注册`);
            }
        }
    }
} )()


ReactDOM.render(<App/>, document.querySelector("#app"));