import ajax from "./ajax";
import url from "./url";

let moneyXuan = null;

class Win {
    constructor() {
        this.money = 0;
        this.name = "liu";
        this.ajaxGetMoney();
    }
    getMoney() {
        return this.money;
    }
    setMoney(money) {
        this.money = money;
        // 引发钱的重新渲染
        if(moneyXuan) moneyXuan(money);
        this.ajaxSetMoney();
    }
    setMoneyXuan(xuan) {
        moneyXuan = xuan;
    }
    ajaxGetMoney() {
        ajax(url + "/nongChang/getMoney?name=liu")
        .then( (data) => {
            const d = JSON.parse(data);
            if(d == null || d.type == "no" || d.data == null) this.money = 500;
            else this.money = +d.data;
            if(moneyXuan) moneyXuan(this.money);
        } ) 
    }
    ajaxSetMoney() {
        const data = this.money;
        const d = JSON.stringify({
            name: 'liu',
            money: data
        })
        ajax(url + "/nongChang/setMoney", "POST", d);
    }
}

const win = new Win();
export default win;

