import React, {useState} from "react";
import "./Shop.css";
import shop from "../../../public/obj_g_c/createShop.js";


function Shop() {
    return <>
        <div className="shop">
            <ul>
                {
                    shop.seedAgg.map( (k, i) => {
                        return <ShangPin k={k} key={i}></ShangPin>;
                    } )
                }
            </ul>
        </div>
    </>
}


// 单个商品信息组件
function ShangPin(props) {
    const [seedNum, setSeedNum] = useState(1);
    return <>
        <li>
            <div className="img">
                <img src={ props.k.seedImg } alt="" prop="" />
            </div>
            <div className="money">￥{props.k.money}</div>
            <div className="btn">
                <button onClick={ () => {
                    shop.buySeed(props.k, seedNum);
                } } className="but">购买</button>
                <div>
                    <span onClick={ () => {
                        setSeedNum( shop.reedNumReduce( seedNum ) )
                    } } className="jian">-</span>
                    <span>{seedNum}</span>
                    <span onClick={ () => {
                        setSeedNum( shop.reedNumIncrease( seedNum ) )} 
                    } className="jia">+</span>
                </div>
            </div>
        </li>
    </>
}


export default Shop;