import React, { useState } from "react";
import "./House.css";
import houseAgg from "../../../public/obj_g_c/createHouse.js";

function House() {
    const [, chongXuan] = useState({});
    return <>
        <div className="house">
            <ul onClick={ () => {
                chongXuan({});
            } }>
                {
                    houseAgg.house.map((k, i) => {
                        return <Fruit k={k} key={i}></Fruit>
                    })
                }
            </ul>
        </div>
    </>
}

// 每个果实对应的组件
function Fruit(props) {
    const house = props.k;
    return <>
        <li className="fruit">
            <div className="img">
                <img src={house.fruitImgSrc} alt="" prop="" />
            </div>
            <div className="num">数量: {house.num}</div>
            <div className="btn">
                <button onClick={ () => {
                    houseAgg.sellFruit( house.name );
                } }>出售</button>
                <span className="money">单价: ￥{house.money}</span>
            </div>
        </li>
    </>
}

export default House;