import ajax from "./ajax.js";
import win from "./window";
import url from "./url";

class House {
    constructor() {
        this.house = [];

        this.ajaxGetMouse();
    }

    // 向仓库中添加果实的方法
    addFruit(crops, num) {
        if( this.fruitJudge(crops.name) ) {
            const index = this.fruitIndex(crops.name);
            this.house[index].num += num;
        }
        else {
            this.house.push({
                name: crops.name,
                num: num,
                money: crops.fruitMoney,
                fruitImgSrc: crops.fruitImgSrc
            })
        }

        this.ajaxSetMouse();
    }
    // 出售果实的方法
    sellFruit(name) {
        const index = this.fruitIndex(name);
        const house = this.house[index];
        // 计算价格
        const m = house.money * house.num;
        this.house.splice(index, 1);
        
        // 获取到当前共有多少金币
        const money = win.getMoney();
        win.setMoney(money + m);

        this.ajaxSetMouse();
    }
    // 判断仓库中有没有某种果实
    fruitJudge(name) {
        const arr = this.house;
        for(let i = 0; i < arr.length; i ++) {
            if(arr[i].name === name) return true;
        }
        return false;
    }
    // 得到某种果实在仓库中的索引
    fruitIndex(name) {
        const arr = this.house;
        for(let i = 0; i < arr.length; i ++) {
            if(arr[i].name === name) return i;
        }
    }
    // 发送请求，获取到对应的仓库数据
    ajaxGetMouse() {
        ajax( url + "/nongChang/getHouse?name=liu")
        .then( (data) => {
            const d = JSON.parse(data);
            if(typeof d.data === "string") d.data = JSON.parse(d.data);
            if(d.type === "ok" && typeof d.data === "object") {
                this.house = d.data;
            }
            console.log(d);
        } )
    }
    ajaxSetMouse() {
        const data = JSON.stringify( this.house );
        const d = JSON.stringify({
            name: 'liu',
            house: data
        })
        ajax( url + "/nongChang/setHouse", "POST", d);
    }
}

const house = new House();

export default house;