import React from "react";
import "./SeedSpecies.css";

import seedPack from "../../../public/obj_g_c/createSeedPack.js";


function Seed() {
    return <>
        <div className="seedSpecies">
            <ul>
                { 
                    seedPack.seedAgg.map((k, i) => {
                        return <SeedRend k={k} key={i}></SeedRend>
                    })
                }
            </ul>
        </div>
    </>
}

// 每一个种子的渲染组件
function SeedRend(props) {
    const seed = props.k;
    return <li>
        <div className="img">
            <img src={seed.seed.seedImg} alt="" prop="" />
        </div>
        <div className="num">数量: {seed.num}</div>
        <button className="species" onClick={ () => {
            React.sum.emit("setExhibitionBoo", "种子包种植");
            seed.seed.plantCrops();
        } }>种植</button>
    </li>
}

export default Seed;