import ajax from "./ajax";
import Seed from "./createSeed";
import crops from "./createCropsAgg.js";
import url from "./url.js";

class SeedPack {
    constructor() {
        this.seedAgg = [];

        this.ajaxGetSeed();
    }
    // 购买种子
    seedNumPlus(seed, num) {
        // 判断种子包中，有没有该种子
        if( this.seedTypeJudge(seed.name) ) {
            const seedIndex = this.seedIndex(seed.name);
            this.seedAgg[ seedIndex ].num += num;
        }
        else {
            this.seedAgg.push({
                name: seed.name,
                num: num,
                seed: seed
            })
        }

        this.ajaxSetSeed();
    }
    // 删除一粒种子
    remove(name) {
        const index = this.seedIndex(name);
        this.seedAgg[index].num -= 1;
        if(this.seedAgg[index].num === 0) {
            this.seedAgg.splice(index, 1);
        }
        this.ajaxSetSeed();
    }
    // 判断种子包中有没有某种类型的种子
    seedTypeJudge(name) {
        for(let i = 0; i < this.seedAgg.length; i ++) {
            if( this.seedAgg[i].seed.name === name) return true;
        }
        return false;
    }
    // 得到某种类型种子在种子包中的索引
    seedIndex(name) {
        for(let i = 0; i < this.seedAgg.length; i ++) {
            if( this.seedAgg[i].seed.name === name) return i;
        }
    }

    // 发送请求，获取到对应的仓库数据
    ajaxGetSeed() {
        ajax(url + "/nongChang/getSeed?name=liu")
        .then( (data) => {
            const d = JSON.parse(data);
            if(typeof d.data === "string") d.data = JSON.parse(d.data);
            if(d.type === "ok" && typeof d.data === "object") {
                for(let i = 0; i < d.data.length; i ++) {
                    const name = d.data[i].name;
                    const num = d.data[i].num;
                    const seed = new Seed( crops.getCrops(name) );
                    this.seedAgg.push({name, num, seed});
                }
            }
            console.log(this.seedAgg);

        } )
    }
    ajaxSetSeed() {
        const data = JSON.stringify( this.seedAgg );
        const d = JSON.stringify({
            name: 'liu',
            seed: data
        })
        ajax(url + "/nongChang/setSeed", "POST", d);
    }
}

const seedPack = new SeedPack();

export default seedPack;

