import React from "react";

class Seed  {
    constructor( crops ) {
        this.name = crops.name;
        this.money = crops.seedMoney;
        this.crops = crops;
        this.seedImg = crops.fruitImgSrc;
    }
    plantCrops() {
        // 调用具体的种植方法进行种植
        React.sum.emit("plantCrops", this.crops);
    }
}

export default Seed;