class CropsAgg {
    constructor(...crops) {
        this.cropsAgg = [...crops];
    }
    getCrops(name) {
        for(let i = 0; i < this.cropsAgg.length; i ++) {
            if(this.cropsAgg[i].name === name) return this.cropsAgg[i];
        }
    }
}

