import React, {useState, useEffect} from "react";
import "./Exhibition.css";

import Shop from "./shop/Shop.js";
import Seed from "./seed/Seed.js";
import SeedSpecies from "./seedSpecies/SeedSpecies.js";
import House from "./house/House.js";


function Exhibition() {
    const [boo, setBoo] = useState("");
    
    // 注册切换状态的方法
    useEffect(() => {
        React.sum.on("setExhibitionBoo", (data) => {
            if(data === boo) setBoo("");
            else setBoo(data);
        });
    }, [boo])

    return <>
        { 
            boo && <div className="exhibition">
                { boo === "商店" && <Shop></Shop> }
                { boo === "种子包" && <Seed></Seed> }
                { boo === "种子包种植" && <SeedSpecies></SeedSpecies> }
                { boo === "仓库" && <House></House> }
            </div> 
        }
    </>
}

export default Exhibition;